  margin-left: -15px;
  margin-right: -15px;
  justify-content: space-around;
}

.advantages .content .block {
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 15px;
  margin-right: 15px;
  width: calc(33.333333333333336% - 30px);
  text-align: center;
  padding-bottom: 65px;
