}

.advantages .content .block:nth-child(2):hover .image {
  display: none;
}

.advantages .content .block:nth-child(2):hover .image.visible-none {
  display: block;
}

.advantages .content .block:hover {
  color: #fff;
  background: #0d164a;
}

.advantages .content .block:hover .image {
  display: none;
}

.advantages .content .block:hover .image.visible-none {
  display: block;
}

.advantages .content .block .image {
  margin-bottom: 40px;
  margin-top: 10px;
}

.advantages .content .block .image.visible-none {
  display: none;
}

.table-bg {
  position: relative;
  z-index: 1;
}

.table-bg::before {
  content: url("..\images\advantage-bg.png");
  display: block;
  position: absolute;
  z-index: -1;
  top: 0;
  right: 0;
  transform: translateY(-40%);
}

.table-bg .table {
  max-width: 1200px;
  margin: 0 auto;
  padding-left: 30px;
  padding-right: 30px;
  margin-bottom: 100px;
}

.table-bg .table .content b {
  font-size: 18px;
  font-family: 'ProximaNova-RegIt';
  color: #45b7e6;
  display: inline-block;
  margin-bottom: 10px;
  font-weight: 400;
}

.table-bg .table .content .wrap {
  border-radius: 10px;
  border: 1px solid #c8c8c8;
  overflow: hidden;
}

.table-bg .table .content .wrap table {
  border-radius: 10px;
  display: block;
  border-collapse: collapse;
  margin: -1px;
}

.table-bg .table .content .wrap table tr.bg {
  background: #f7f7f7;
  border-radius: 10px;
  border-collapse: collapse;
  border: 1px solid #c8c8c8;
}

.table-bg .table .content .wrap table tr th {
  padding: 20px 11px;
  font-size: 12px;
  color: #1f1f1f;
  font-weight: 700;
  font-family: 'Open Sans';
  text-align: center;
  border-collapse: collapse;
  border: 1px solid #c8c8c8;
}

.table-bg .table .content .wrap table td {
  padding: 20px 7px;
  border: 1px solid #c8c8c8;
  border-collapse: collapse;
  text-align: center;
  font-size: 12px;
  color: #3c3c3c;
  font-weight: 600;
  font-family: 'Open Sans';
}

.table-bg .table .content .wrap table td li {
  color: #3c3c3c;
}

.table-bg .table .content .wrap table td.left {
  text-align: left;
  padding-left: 13px;
}

.table-bg .table .content .wrap table td.butn {
  padding-right: 40px;
  padding-left: 40px;
}

.table-bg .table .content .wrap table td a {
  padding: 14px 28px;
  color: #0e0e0e;
  background: #f9ee3a;
  border-radius: 10px;
  font-size: 14px;
  font-weight: 700;
}

.table-bg .table .content .wrap table td a:hover {
  background: #0d164a;
  color: #fff;
  transition: 1s;
}

