    align-items: center;
  }

  header {
    padding-left: 0;
  }

  header {
    padding-right: 0;
  }

  header .logo {
    width: calc(33.333333333333336% - 30px);
  }

  header .logo {
    padding-left: 15px;
  }

  header .menu {
